<template>

<div>

<form action="" method="post" id="_frmForm" name="frmForm" @submit.prevent="addBoard">
<table class="content_table">
<colgroup>
		<col style="width:30%;" />
		<col style="width:70%;" />							
</colgroup>	

<tr>
<th>제목</th>
<td><input data-msg="제목" type="text" name="title"  id="_title" size="20" v-model="ctitle" style="width:30%"/></td>
</tr>
<tr>
<th>비밀번호</th>
<td><input data-msg="비밀번호" type="password" name="password"  id="_password" size="20" v-model="cpw" style="width:30%"/></td>
</tr>
<tr>
<th>작성자</th>
<td><input data-msg="작성자" type="text" name="writer"  id="_writer" size="20" v-model="cwriter" style="width:30%"/></td>
</tr>
<tr>
<th>내용</th>
<td><textarea data-msg="내용" type="text" name="contents"  id="_contents" size="30" v-model="ccontents" cols="100" rows="20" style="resize: none;" /></td>
</tr>

<tr>
<td colspan="2" style="height:50px; text-align:center;">
<button @click="addBoard()" type="button" name="button">게시판 추가</button></td>
</tr>
</table>
</form>

</div>

</template>

<script>
import http from "../http-common";

export default {
	name: "add-board",
	data() {
		return {
		ctitle: '',
		cpw:'',
		cwriter: '',
		ccontents: ''
		};
	},
	filters: {
		salarydecimal (value) {
			var a=parseInt(value);
			return a.toFixed(2);
		}
	}, 
	mounted () {
	http
		.get('/findAllBoard')
		.then(response => (this.info = response.data))
		.catch(() => {
			this.errored = true
		})
		.finally(() => this.loading = false);
	},
	methods: {
	addBoard() {
		if(this.ctitle==''){ alert('제목을 입력하세요.'); return ;}
		if(this.ccontents==''){ alert('내용을 입력하세요.'); return ;}
		
		http.post('/addBoard', {
			title: this.ctitle,
			pw: this.cpw,
			contents: this.ccontents,
			writer: this.cwriter
		} 
		).then(response => {
				if (response.data.state==null) {
					alert("추가를 하였습니다.");
					this.$router.push("/board");
				}else{
					alert("추가를 하지 못했습니다.");
				}
		});
		this.submitted = true;
	}
  }
}
</script>

<style>
.submitform {
  max-width: 300px;
  margin: auto;
}
</style>
