<template>
  <div>
    <div>
      <table class="list_table">
        <col width="10%" />
        <col width="20%" />
        <col width="20%" />
        <col width="15%" />
        <col width="15%" />
        <col width="15%" />
        <tr>
          <th>게시판 번호</th>
          <th>제목</th>
          <th>작성자</th>
          <th>작성일</th>
          <th>조회수</th>
          <th>처리</th>
        </tr>
        <tr v-for="board in info" class="nicecolor" :key="board.num">
          <td v-html="board.num" @click="show_detail(board.num)"></td>
          <td v-html="board.title" @click="show_detail(board.num)"></td>
          <td v-html="board.writer"></td>
          <td v-html="board.date"></td>
          <td v-html="board.hits"></td>
          <td class="button">
            <input @click="update_board(board.num,board.pw)" type="button" class="blue" value="수정" /> |
           <input @click="delete_board(board.num,board.pw)" type="button" class="blue" value="삭제" />
          </td>
        </tr>
      </table>
      <div style="position: absolute; right:60px;">
     <input type="password" v-model="pw" size="33" placeholder="처리할 게시판의 비밀번호를 입력해주세요"/>
      </div>
    </div>
  </div>
</template>

<script>
import http from "../http-common";
export default {
  name: "board-list",
  data() {
    return {
      pw: "",
      upHere: false,
      info: [],
      loading: true,
      errored: false
    };
  },
  methods: {
    retrieveBoard() {
      http
        .get("/findAllBoard")
        .then(response => (this.info = response.data))
        .catch(() => {
          this.errored = true;
        })
        .finally(() => (this.loading = false));
    },
    refreshList() {
      this.retrieveBoard();
    },
    show_detail: function(boardnum) {
      alert(boardnum + "상세보기");
      this.$router.push("/detailboard/" + boardnum);
    },
    update_board: function(boardnum,boardpw) {
      if(this.pw==boardpw)
      this.$router.push("/updateboard/" + boardnum);
      else
      alert("비밀번호 틀렸습니다");
    },
    delete_board(boardnum,boardpw) {
      if(this.pw==boardpw){
      http
        .delete("/deleteBoard/" + boardnum)
        .then(response => {
          if (response.data.state == null) {
            alert("삭제처리를 하였습니다.");
            this.refreshList();
          }

        })
        .catch(() => {
          this.errored = true;
        })
        .finally(() => (this.loading = false));
    }
    else{
      alert("비밀번호 틀렸습니다.");
    }
    }
  },
  filters: {
    salarydecimal(value) {
      var a = parseInt(value);
      return a.toFixed(2);
    }
  },
  mounted() {
    this.refreshList();
  }
};
</script>
