<template>
  <div>
    <div class="search_box">
      <h3>
        <span style="color:blue;">게시판 정보 수정</span>
      </h3>
      <button class="btn btn-success" @click="show_detail()">게시판 정보 보기</button>
    </div>

    <div v-if="!submitted">
      <form action method="post" id="_frmForm" name="frmForm" @submit.prevent="updateEmployee">
        <table class="content_table">
          <colgroup>
            <col style="width:30%;" />
            <col style="width:70%;" />
          </colgroup>
          <tr>
            <th>제목</th>
            <td>
              <input
                data-msg="제목"
                type="text"
                name="title"
                id="_title"
                size="20"
                v-model="cboard.title"
                style="width:30%"
              />
            </td>
          </tr>
          <tr>
            <th>비밀번호</th>
            <td>
              <input
                data-msg="비밀번호"
                type="password"
                name="pw"
                id="_pw"
                size="20"
                v-model="cboard.pw"
                style="width:30%"
              />
            </td>
          </tr>
          <tr>
            <th>작성자</th>
            <td>
              <input
                data-msg="작성자"
                type="text"
                name="writer"
                id="_writer"
                size="30"
                v-model="cboard.writer"
                style="width:30%"
              />
            </td>
          </tr>
          <tr>
            <th>내용</th>
            <td>
              <textarea 
              data-msg="내용" 
              type="text" 
              name="contents"  
              id="_contents" 
              size="30" 
              v-model="cboard.contents" 
              cols="100" rows="20" 
              style="resize: none;" />
            </td>
          </tr>
          <tr>
            <td colspan="2" style="height:50px; text-align:center;">
              <button type="submit" name="button" class="btn btn-success">정보수정하기</button>
            </td>
          </tr>
        </table>
      </form>
    </div>

    <div class="search_box" v-else>
      <h3>
        <span style="color:blue;">성공적으로 수정하였습니다!</span>
      </h3>
    </div>
  </div>
</template>

<script>
import http from "../http-common";

export default {
  name: "updateboard",
  props: ["num"],
  data() {
    return {
      info: null,
      loading: true,
      errored: false,
      deps: null,
      titls: null,
      pw: null,
      cboard: {
        title: "",
        writer: "",
        date: "",
        contents: "",
        pw: ""
      },
      submitted: false
    };
  },
  mounted() {
    http
      .get("/detailBoard/" + this.num)
      .then(response => {
        this.cboard = response.data;
      })
      .catch(() => {
        this.errored = true;
      })
      .finally(() => (this.loading = false));
  },
  methods: {
    show_detail: function() {
      alert(this.num + "상세보기");
      //아래를 수정했다.
      this.$router.push("/detailboard/" + this.cboard.num);
    },
    updateEmployee() {
      if (this.cboard.title == "") {
        alert("제목을 입력하세요.");
        return;
      }
       if (this.cboard.pw == "") {
        alert("비밀번호를 입력하세요.");
        return;
      }
      if (this.cboard.writer == "") {
        alert("작성자를 입력하세요.");
        return;
      }
      if (this.cboard.date == "") {
        alert("작성일을 입력하세요.");
        return;
      }
      if (this.cboard.contents == "") {
        alert("내용을 입력하세요.");
        return;
      }

      http
        .post("/updateBoard", {
          num: this.cboard.num,
          title: this.cboard.title,
          writer: this.cboard.writer,
          date: this.cboard.date,
          contents: this.cboard.contents,
          pw: this.cboard.pw
        })
        .then(response => {
          if (response.data.state == null) {
            alert("정보를 수정 하였습니다.");
          } else {
            alert("정보를 수정 하지 못했습니다.");
          }
        });
      this.submitted = true;
    },
  }
};
</script>

<style>
.submitform {
  max-width: 300px;
  margin: auto;
}
</style>
