import Vue from "vue";
import Router from "vue-router";
import BoardList from "./components/BoardList.vue";
import SearchBoardNum from "./components/SearchBoardNum.vue";
import AddBoard from "./components/AddBoard.vue";
import DetailBoard from "./components/DetailBoard.vue";
import UpdateBoard from "./components/UpdateBoard.vue";

Vue.use(Router);

export default new Router({
  mode: "history",
  routes: [
    {
      path: "/board",
      name: "boards",
      alias: "/board",
      component: BoardList
    },
    {
      path: "/searchboardnum",
      name: "searchboardnum",
      component: SearchBoardNum
    }
    ,
    {
      path: "/add",
      name: "add",
      component: AddBoard
    }
    ,
    {
      path: '/detailboard/:num',
      name: 'detailboard',
      component: DetailBoard,
      props: true,
    }
    ,
    {
      path: '/updateboard/:num',
      name: 'updateboard',
      component: UpdateBoard,
      props: true,
    }
    ,
  ]
});