<template>
  <div>
    <table class="list_table">
      <col width="10%" />
      <col width="15%" />
      <col width="15%" />
      <col width="15%" />
      <col width="15%" />
      <col width="40%" />
      <tr>
        <th>게시판 번호</th>
        <th>제목</th>
        <th>작성자</th>
        <th>작성일</th>
        <th>조회수</th>
        <th>내용</th>
      </tr>
      <tr @click="show_detail(cboard.num)">
        <td v-html="cboard.num"></td>
        <td v-html="cboard.title"></td>
        <td v-html="cboard.writer"></td>
        <td v-html="cboard.date"></td>
        <td v-html="cboard.hits"></td>
        <td v-html="cboard.contents"></td>
      </tr>
    </table>
  </div>
</template>

<script>
import http from "../http-common";

export default {
  name: "detailboard",
  props: ["num"],
  data() {
    return {
      info: [],
      loading: true,
      errored: false,
      cid: "",
      cboard: {}
    };
  },
  filters: {
    salarydecimal(value) {
      var a = parseInt(value);
      return a.toFixed(2);
    }
  },
  methods: {
    show_init: function() {
      http
        .get("/detailBoard/" + this.num)
        .then(response => (this.cboard = response.data))
        .catch(() => {
          this.errored = true;
        })
        .finally(() => (this.loading = false));
    }
  },
  mounted() {
    this.show_init();
  }
};
</script>

<style>
.searchform {
  max-width: 300px;
  margin: auto;
}
.search-result {
  margin-top: 20px;
  text-align: left;
}
</style>