<template>
  <div>
    <div class="search_box">
      <select name="num" v-model="cnum" @change="findbynum" v-cloak>
        <option v-for="board in info" v-bind:value="board.num" :key="board.num">{{ board.num }}</option>
      </select>
    </div>
    <table class="list_table">
      <col width="10%" />
      <col width="15%" />
      <col width="15%" />
      <col width="15%" />
      <col width="35%" />
      <tr>
        <th>게시판 번호</th>
        <th>제목</th>
        <th>작성자</th>
        <th>작성일</th>
        <th>내용</th>
      </tr>
      <tr>
        <td v-html="cboard.num"></td>
        <td v-html="cboard.title"></td>
        <td v-html="cboard.writer"></td>
        <td v-html="cboard.date"></td>
        <td v-html="cboard.contents"></td>
      </tr>
    </table>
  </div>
</template>

<script>
import http from "../http-common";

export default {
  name: "search-board-num",
  data() {
    return {
      info: [],
      loading: true,
      errored: false,
      cnum: 0,
      cboard: {}
    };
  },
  filters: {
    salarydecimal(value) {
      var a = parseInt(value);
      return a.toFixed(2);
    }
  },
  mounted() {
    http
      .get("/findAllBoard")
      .then(response => (this.info = response.data))
      .catch(() => {
        this.errored = true;
      })
      .finally(() => (this.loading = false));
  },
  methods: {
    findbynum() {
      http
        .get("/detailBoard/" + this.cnum)
        .then(response => (this.cboard = response.data))
        .catch(() => {
          this.errored = true;
        })
        .finally(() => (this.loading = false));
    }
  }
};
</script>

<style>
.searchform {
  max-width: 300px;
  margin: auto;
}
.search-result {
  margin-top: 20px;
  text-align: left;
}
</style>